@import '../config';

.margin-color {
	margin-top: -70px;
	color: rgb(111, 111, 111);
	font-weight: 700;
}

.pallino-nero{
	background-color: @highlight-color;
	color: transparent;
	font-size: 0;
	height: 16px;
	width: 16px;
	border-radius: 50%;

}

.pallino-verde{
	background-color: @slider-bar-color;
	color: transparent;
	font-size: 0;
	height: 16px;
	width: 16px;
	border-radius: 50%;

}

.slider-bar-container {
	position: relative;
	.slider-bar {
		height: @slider-bar-height;
		width: 100%;
		background-color: @slider-bar-color;
		position: relative;
		border-radius: @slider-bar-height / 2;
		.slider-bar-highlight {
			position: absolute;
			left: 0;
			width: 50%;
			height: 100%;
			background-color: @highlight-color;
			border-radius: @slider-bar-height / 2;
		}
	}
	.slider-labels-principal {
		overflow: hidden;
		margin-top: -34px;
		width: 100%;
		display: flex;
		font-size: 18px;
		font-weight: 900;
		.storico {
			text-align: center;
			width: 50%;
			.label {
				border: 1px solid rgb(111, 111, 111);
				padding-left: 18px;
				padding-right: 18px;
				background-color: rgb(111, 111, 111);
				color: white;
				border-radius: 0;
			}
		}
		.previsionale {
			text-align: center;
			width: 50%;
      min-width: 140px;
			.label {
				border: 1px solid rgb(111, 111, 111);
				padding-left: 18px;
				padding-right: 18px;
				background-color: green;
				color: white;
				border-radius: 0;
			}
		}
	}
	.slider-labels {
		margin-top: 0px;
		width: 100%;
		display: flex;
		&::after {
			display: block;
			position: absolute;
			content: '';
			height: 1px;
			width: @slider-bar-height / 4;
			background-color: #BBB;
			top: @slider-bar-height + 2px;
			right: 0;
		}
		.slider-label-item {
			// flex: 1;
			text-align: left;
			// font-size:
			span {
				transform: translate(-25%, 0);
				position: relative;
				display: inline-block;
			}
			&:first-child {
				span {
					// transform: translate(-20%, 0);
				}
			}
			&:last-child {
				span {
					// transform: translate(20%, 0);
				}
			}
			&::after {
				display: block;
				position: absolute;
				content: '';
				height: 1px;
				width: @slider-bar-height / 4;
				background-color: #BBB;
				top: @slider-bar-height + 2px;
			}
		}
	}
	.slider-points {
		margin-top: 60px;
		// width: 99.3%;
		width: 100%;
		display: flex;
		&::after {
			display: block;
			position: absolute;
			content: '';
			height: 1px;
			width: @slider-bar-height / 4;
			background-color: #BBB;
			top: @slider-bar-height + 2px;
			right: 0;
		}
		.slider-label-item {
			flex: 1;
			text-align: left;
			// font-size:
			span {
				transform: translate(-50%, 0);
				position: relative;
				display: inline-block;
			}
			&:first-child {
				span {
					transform: translate(-20%, 0);
				}
			}
			&:last-child {
				span {
					transform: translate(20%, 0);
				}
			}
			&::after {
				display: block;
				position: absolute;
				content: '';
				height: 1px;
				width: @slider-bar-height / 4;
				background-color: #BBB;
				top: @slider-bar-height + 2px;
			}
		}
	}
}
